export default {
  userInput: '',
  pageNumber: 0,
  async getPhotos() {
    this.pageNumber += 1;
    try {
      const response = await fetch(
        `https://pixabay.com/api/?image_type=photo&orientation=horizontal&q=${this.userInput}&page=${this.pageNumber}&per_page=12&key=20256016-e075cd8f3fca3c08548fa1ef3`,
      );
      return response.json();
    } catch (err) {
      throw err;
    }
  },
  resetPage() {
    this.pageNumber = 0;
  },
  get input() {
      this.userInput;
  },
  set input(newInput) {
      this.userInput = newInput;
  },
};
