import 'basiclightbox/dist/basicLightbox.min.css';
import './styles.css';
import apiService from './js/apiService'
import imageTemplate from './image-template.hbs';

const basicLightbox = require('basiclightbox');
const refs = {
  form: document.querySelector('.search-form'),
  gallery: document.querySelector('.gallery'),
  searchBtn: document.querySelector('.search-button'),
  loadMoreBtn: document.querySelector('.load-more-btn'),
}

const handleInput = e => {
  e.preventDefault()
  
  const form = e.currentTarget;
  apiService.input = form.elements.query.value;

  refs.gallery.innerHTML = '';
  form.reset();
  
  apiService.resetPage();
  apiService.getPhotos()
  .then(photos => {
    insertStats(photos)
  })
};


const handleBtn = () => {
  refs.loadMoreBtn.style.opacity = '0';
  apiService.getPhotos()
  .then(photos => {
    insertStats(photos)

    
    window.scrollTo(0, document.body.scrollHeight);
    setTimeout(() => {
      refs.loadMoreBtn.style.opacity = '1';
    }, 1000)
  })
}

const insertStats = ({ hits }) => {
  
const mockup = imageTemplate(hits);
refs.gallery.insertAdjacentHTML('beforeend', mockup);
refs.loadMoreBtn.classList.replace('load-more-btn', 'load-more-btn--active');

};

const openOverlay = e => {

  if (e.target.nodeName !== 'IMG') {
    return
  };

  const clickedImageRef = e.target.dataset.lgpicture;
  
  const instance = basicLightbox.create(`
	<img src="${clickedImageRef}" width="800" height="600">
`)

instance.show()
}

refs.form.addEventListener('submit', handleInput);
refs.loadMoreBtn.addEventListener('click', handleBtn);
refs.gallery.addEventListener("click", openOverlay);